import React from "react";
import {
  Row,
  Col,
  Input,
  Form,
  Button,
  Typography,
  message,
  Checkbox,
} from "antd";
import "antd/dist/antd.min.css";
import axios from "axios";
const { Title, Text } = Typography;
const isValidDomain = require("is-valid-domain");

export default class ExportClass extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isDisabled: false,
      isFilter: false,
    };
  }

  check = async () => {
    this.setState({ isDisabled: true });
    const { domains } = await this.form.validateFields();
    const { isFilter } = this.state;
    const apiKey = "c1da62fa-638c-41a7-b251-568e55dd3574";
    let listDomain = domains.trim().split(",");
    if (listDomain.length > 10)
      return message.error("Please enter less than 10 domains");
    if (isFilter) {
      listDomain = listDomain.filter(
        (item, index) => listDomain.indexOf(item) === index
      );
    }

    const registered = [];
    const unregistered = [];
    const error = [];

    listDomain.forEach((domain, index) => {
      const apiUrl = `https://api.builtwith.com/free1/api.json?KEY=${apiKey}&LOOKUP=${domain}`;
      setTimeout(() => {
        axios.get(apiUrl).then((res) => {
          const { data } = res;
          if ((!isValidDomain(domain), { subdomain: false })) {
            error.push(domain);
            this.form.setFieldsValue({
              error: error.join(","),
            });
            return;
          }
          if (data.hasOwnProperty("Errors")) {
            if (data.Errors[0].Code === -10) {
              unregistered.push(domain);
              this.form.setFieldsValue({
                unregistered: unregistered.join(","),
              });
            } else {
              error.push(domain);
              this.form.setFieldsValue({
                error: error.join(","),
              });
            }
          } else {
            registered.push(domain);
            this.form.setFieldsValue({
              registered: registered.join(","),
            });
          }
        });
      }, 2000 * index);
    });
    setTimeout(() => {
      this.setState({ isDisabled: false });
    }, listDomain.length * 2000);
  };

  render = () => {
    return (
      <Row
        type="flex"
        justify="center"
        align="middle"
        style={{ minHeight: "100vh" }}
      >
        <Col span={20}>
          <center>
            <Title level={2}>DWHOIS</Title>
            <Text type="secondary">A product of D Tech</Text>
          </center>
          <Form
            style={{
              marginTop: "20px",
            }}
            layout="vertical"
            ref={(ref) => (this.form = ref)}
          >
            <Form.Item
              name={"domains"}
              label={
                'Enter your domain, multiple domain names separated by "," . Example : domain1.com, domain2.com, domain3.com ( Up to 10 domains 1 time). Not duplicate page.'
              }
              rules={[
                {
                  required: true,
                  message: "Please enter your domain name!",
                },
              ]}
            >
              <Input.TextArea spellCheck={false} rows={5} />
            </Form.Item>
            <Form.Item name={"filter"}>
              <Checkbox
                defaultChecked={this.state.isFilter}
                onChange={() => (this.state.isFilter = !this.state.isFilter)}
              >
                Filter duplicates
              </Checkbox>
            </Form.Item>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                disabled={this.state.isDisabled}
                onClick={this.check}
              >
                Check
              </Button>
            </Form.Item>
            <Row gutter={16}>
              <Col xs={24} md={8} xl={8}>
                <Form.Item name={"registered"} label={"Domain is registered"}>
                  <Input.TextArea rows={5} readOnly />
                </Form.Item>
              </Col>
              <Col xs={24} md={8} xl={8}>
                <Form.Item
                  name={"unregistered"}
                  label={"Domain is unregistered"}
                >
                  <Input.TextArea rows={5} readOnly />
                </Form.Item>
              </Col>
              <Col xs={24} md={8} xl={8}>
                <Form.Item name={"error"} label={"Error"}>
                  <Input.TextArea rows={5} readOnly />
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row>
    );
  };
}
